<template>
  <div>
    <div class="container animated fadeIn">
      <div class="text-left">
        <router-link class="btn btn-light" to="/domains">Voltar</router-link>
        <br />
        <br />
        <h2 class="text-center">{{ domain }}</h2>
        <br />
        <div class="card">
          <div class="card-body">
            <ul class="list-group">
              <li
                class="list-group-item"
                v-for="domain in domains"
                :key="domain.extension"
              >
                <div class="row">
                  <div class="col-md-6 text-left aling-center">
                    {{ domain.extension }}
                  </div>
                  <div class="col-md-3 aling-center">
                    <h5>
                      <span
                        :class="[
                          'badge',
                          domain.available ? 'badge-success' : 'badge-danger'
                        ]"
                        >{{
                          domain.available ? 'Disponível' : 'Indisponivel'
                        }}</span
                      >
                    </h5>
                  </div>
                  <div class="col-md-3 text-right">
                    <a
                      :class="[
                        'btn mr-2',
                        domain.available
                          ? 'btn-dark'
                          : 'btn-default text-danger disabled'
                      ]"
                      :href="domain.checkout"
                      target="_blank"
                    >
                      <span class="fa fa-shopping-cart"></span>
                    </a>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios/dist/axios'

export default {
  name: 'DomainView',
  props: ['domain'],
  data() {
    return { domains: [] }
  },
  created() {
    axios({
      url: 'http://localhost:4000',
      method: 'post',
      data: {
        query: `
          mutation ($name: String!) {
            domains: generateDomain(name: $name) {
              name
              extension
              checkout
              available
            }
          }
        `,
        variables: {
          name: this.domain
        }
      }
    }).then(res => {
      const query = res.data
      this.domains = query.data.domains
    })
  }
}
</script>
