<template>
  <div>
    <div class="row">
      <div class="col-md-6">
        <!-- <div
          class="btn btn-primary"
          data-toggle="modal"
          data-target="#staticBackdrop"
        >
          <i class="fa fa-plus mr-2"></i>
          {{ btnTitle }}
        </div> -->
      </div>
      <div
        class="col-md-6"
        style="display: flex;justify-content: center;align-items: center;"
      >
        <div
          class="btn-gmail"
          data-toggle="modal"
          data-target="#staticBackdrop"
        >
          <i class="fa fa-user-plus"></i>
        </div>
      </div>
    </div>
    <div
      class="modal fade"
      id="staticBackdrop"
      data-backdrop="static"
      tabindex="-1"
      role="dialog"
      aria-labelledby="staticBackdropLabel"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="staticBackdropLabel">{{ btnTitle }}</h5>
          </div>
          <div class="modal-body">
            <form>
              <div class="form-group">
                <label for="name" class="col-form-label">Nome:</label>
                <input
                  type="text"
                  class="form-control text-uppercase"
                  id="name"
                  v-model="user.name"
                />
              </div>
              <div class="form-group">
                <label for="email" class="col-form-label">E-Mail:</label>
                <input
                  type="email"
                  class="form-control"
                  id="email"
                  v-model="user.email"
                />
              </div>
              <div class="form-group">
                <div class="custom-file mt-4">
                  <input
                    type="file"
                    class="custom-file-input"
                    id="validatedCustomFile"
                    @change="e => handleFile(e)"
                    required
                  />
                  <label class="custom-file-label" for="validatedCustomFile">{{
                    file
                  }}</label>
                </div>
                <img
                  :src="img"
                  :alt="file"
                  class="rounded mx-auto d-block mt-4 mb-1"
                  v-if="isImg"
                  style="height: 180px; width: 100%; display: block;"
                />
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <div
              class="btn-gmail mr-3 mt-1"
              data-dismiss="modal"
              @click="handleClose"
            >
              <i class="fa fa-close"></i>
            </div>
            <div
              class="btn-gmail ml-3 mt-1"
              data-dismiss="modal"
              @click="addUser(user.name, user.email, b64)"
            >
              <i class="fa fa-save"></i>
            </div>
            <!-- <div
              class="btn btn-light"
              data-dismiss="modal"
              @click="handleClose"
            >
              <i class="fa fa-close"></i>
            </div>
            <div
              class="btn btn-secondary"
              data-dismiss="modal"
              @click="addUser(user.name, user.email, b64)"
            >
              <i class="fa fa-save"></i>
            </div> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import image2base64 from 'image-to-base64'
import { imgDefault } from '../../helpers/value'
export default {
  name: 'UserModalAdd',
  props: ['btnTitle'],
  data() {
    return {
      user: {
        name: '',
        email: ''
      },
      modal: false,
      file: 'Selecione uma imagem.',
      img: '',
      isImg: false,
      b64: imgDefault
    }
  },
  methods: {
    addUser: async function(name, email, b64) {
      await this.$emit('addUser', { name, email, image: b64 })
      this.handleClose()
      this.$emit('alert', {
        type: 'alert-success',
        title: 'Sucesso!',
        msg: 'Salvo com Sucesso.',
        isShow: true,
        time: 6000
      })
      // this.$store.dispatch('user/addUser', { name, email, image: b64 })
    },
    handleClose: function() {
      this.file = 'Selecione uma imagem.'
      this.img = ''
      this.isImg = false
      this.b64 = imgDefault
      this.user = {}
      this.modal = false
    },
    handleFile: async function(e) {
      // console.log(e)
      try {
        const {
          target: { files }
        } = e
        const file = files[0]
        const type = files[0].type
        const url = URL.createObjectURL(file)
        if (files[0].name == undefined) {
          this.file = 'Selecione uma imagem.'
          this.img = ''
          this.isImg = false
          this.b64 = imgDefault
          return
        }
        this.file = files[0].name
        this.img = url
        this.isImg = true
        await image2base64(url)
          .then(res => (this.b64 = `data:${type};base64,${res}`))
          .catch(error => console.log(error))
      } catch (error) {
        this.file = 'Selecione uma imagem.'
        this.img = ''
        this.isImg = false
        this.b64 = imgDefault
      }
    }
  }
}
</script>

