<script>
import { mapState, mapActions } from 'vuex'
import UserModalAdd from './UserModalAdd'
import Alert from './Alert'
import SideUser from './SideUser'
import UserApiJava from './UserApiJava'

export default {
  name: 'UserList',
  components: {
    UserModalAdd,
    Alert,
    SideUser,
    UserApiJava
  },
  data() {
    return {
      activeSideUser: false,
      id: '',
      alert: {
        type: '',
        title: '',
        msg: '',
        isShow: false
      }
    }
  },
  methods: {
    ...mapActions('user', ['addUser', 'generateUsers']),
    alertMsg: function(alert) {
      this.alert.type = alert.type
      this.alert.title = alert.title
      this.alert.msg = alert.msg
      this.alert.isShow = alert.isShow
      setTimeout(() => {
        this.alert.isShow = false
      }, alert.time)
    },
    deleteUser(user) {
      this.$store.dispatch('user/deleteUser', user, { root: true })
      this.alertMsg({
        type: 'alert-success',
        title: 'Sucesso!',
        msg: 'Deletado com Sucesso.',
        isShow: true,
        time: 6000
      })
    },
    openUser({ id }) {
      this.$router.push({
        path: `/users/${id}`
      })
    },
    view({ id }) {
      this.id = id
      this.activeSideUser = true
    }
  },
  computed: {
    ...mapState('user', ['users'])
  },
  created() {
    this.$store.dispatch('user/generateUsers', { root: true })
  }
}
</script>

