<template>
  <div>
    <h5>
      {{ title }}
      <span class="badge badge-info">{{ items.length }}</span>
    </h5>
    <div class="card">
      <div class="card-body">
        <div class="input-group">
          <input
            class="form-control"
            type="text"
            placeholder="Digite o item"
            v-model="description"
            @keyup.enter="addItem(type, description)"
          />
          <div class="input-group-append">
            <div class="btn btn-secondary" @click="addItem(type, description)">
              <span class="fa fa-plus"></span>
            </div>
          </div>
        </div>
        <br />
        <ul class="list-group">
          <li class="list-group-item" v-for="item in items" :key="item.id">
            <div class="row">
              <div class="col-md text-left aling-center">
                {{ item.description.toUpperCase() }}
              </div>
              <div class="col-md text-right">
                <div class="btn btn-light" @click="deleteItem(item)">
                  <span class="fa fa-trash"></span>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AppItemList',
  props: ['title', 'type', 'items'],
  data() {
    return {
      description: ''
    }
  },
  methods: {
    addItem(type, description) {
      this.$emit('addItem', {
        type,
        description
      })
      this.description = ''
    },
    deleteItem(item) {
      this.$emit('deleteItem', item)
    }
  }
}
</script>

<style>
.aling-center {
  align-items: center;
  align-content: center;
  display: flex;
}
</style>
