<template>
  <div>
    <div id="main">
      <div class="container animated fadeIn">
        <div class="row">
          <div class="col-md mb-2">
            <AppItemList
              title="Prefixos"
              type="prefix"
              :items="items.prefix"
              @addItem="addItem"
              @deleteItem="deleteItem"
            ></AppItemList>
          </div>
          <div class="col-md mb-2">
            <AppItemList
              title="Sufixos"
              type="sufix"
              :items="items.sufix"
              @addItem="addItem"
              @deleteItem="deleteItem"
            ></AppItemList>
          </div>
        </div>
        <br />
        <h5>
          Domínios <span class="badge badge-info">{{ domains.length }}</span>
        </h5>
        <div class="card">
          <div class="card-body">
            <ul class="list-group">
              <li
                class="list-group-item"
                v-for="domain in domains"
                :key="domain.name"
              >
                <div class="row">
                  <div class="col-md-6 text-left aling-center">
                    {{ domain.name.toUpperCase() }}
                  </div>
                  <div class="col-md-3 aling-center">
                    <h5>
                      <span
                        :class="[
                          'badge',
                          domain.available ? 'badge-success' : 'badge-danger'
                        ]"
                        >{{
                          domain.available ? 'Disponível' : 'Indisponivel'
                        }}</span
                      >
                    </h5>
                  </div>
                  <div class="col-md-3 text-right">
                    <a
                      class="btn btn-dark mr-2"
                      :href="domain.checkout"
                      target="_blank"
                    >
                      <span class="fa fa-shopping-cart"></span>
                    </a>
                    <div class="btn btn-light" @click="openDomain(domain)">
                      <span class="fa fa-search"></span>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import AppItemList from './AppItemList'
export default {
  name: 'DomainList',
  components: {
    AppItemList
  },
  data() {
    return {}
  },
  methods: {
    ...mapActions('domain', [
      'addItem',
      'deleteItem',
      'getItems',
      'generateDomains'
    ]),
    openDomain(domain) {
      this.$router.push({
        path: `/domains/${domain.name}`
      })
    }
  },
  computed: {
    ...mapState('domain', ['items', 'domains'])
  },
  mounted() {
    this.$store.dispatch('domain/getItems', 'prefix', { root: true })
    this.$store.dispatch('domain/getItems', 'sufix', { root: true })
    this.$store.dispatch('domain/generateDomains', { root: true })
  }
}
</script>

<style>
.aling-center {
  align-items: center;
  align-content: center;
  display: flex;
}
</style>
