<template>
  <div>
    <div
      class="overlayUser"
      :class="{ active: active }"
      style="top: 0px;left: 0px;"
      @click="close"
    ></div>
    <nav id="sideuser" :class="{ active: active }">
      <div id="dismissuser" @click="close">
        <i class="fa fa-times"></i>
      </div>
      <div class="sideuser-header">
        <h6>{{ name.toUpperCase() }}</h6>
      </div>
      <div>
        <img class="img-fluid img-user" :src="image" :alt="email" />
        <div class="mt-4 ml-4">
          <div class="mb-2">
            <table>
              <tr>
                <td class="pr-3 pb-3">NOME:</td>
                <td class="pb-3 text-white-50">
                  {{ name.toUpperCase() }}
                </td>
              </tr>
              <tr>
                <td class="pr-3 pb-3">E-MAIL:</td>
                <td class="pb-3 text-white-50">
                  {{ email }}
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </nav>
  </div>
</template>
<script>
import './styles/style.sideuser.css'
// import axios from 'axios/dist/axios'
import base_url from '../../config'
export default {
  name: 'SideUser',
  props: ['id', 'ativo'],
  data() {
    return {
      active: false,
      name: '',
      email: '',
      image: ''
    }
  },
  methods: {
    close() {
      this.active = false
      this.$emit('clickClose')
    }
  },
  computed: {},
  watch: {
    ativo: function(val) {
      this.active = val
    },
    id: function(val) {
      this.$http
        .post(base_url.api, {
          query: `
            query ($id: Int!) {
              user(id: $id) {
                id
                name
                email
                image
              }
            }
          `,
          variables: {
            id: parseInt(val)
          }
        })
        .then(res => {
          const query = res.data
          const user = query.data.user
          this.name = user[0].name
          this.email = user[0].email
          this.image = user[0].image
        })

      // axios({
      //   url: base_url.api,
      //   method: 'post',
      //   data: {
      //     query: `
      //       query ($id: Int!) {
      //         user(id: $id) {
      //           id
      //           name
      //           email
      //           image
      //         }
      //       }
      //     `,
      //     variables: {
      //       id: parseInt(val)
      //     }
      //   }
      // }).then(res => {
      //   const query = res.data
      //   const user = query.data.user
      //   this.name = user[0].name
      //   this.email = user[0].email
      //   this.image = user[0].image
      // })
    }
  }
}
</script>

