<script>
import axios from 'axios/dist/axios'
import base_url from '../../config'
import './style/style.css'
import Test from '../Test'

export default {
  name: 'DeviceList',
  components: {
    InputTest: Test
  },
  data() {
    return {
      time: '',
      username: 'admin',
      password: '',
      isLogin: true,
      isError: false,
      devices: [],
      devicesBlocked: [],
      ativos: [],
      inativos: []
    }
  },
  methods: {
    logout: function() {
      clearInterval(this.time)
      this.isLogin = true
      this.isError = false
      localStorage.setItem('isLogin', true)
      localStorage.setItem('isError', false)
    },
    login: function() {
      axios({
        url: `${base_url.roteador}/v1/system/login`,
        method: 'post',
        data: {
          username: this.username,
          password: this.password
        }
      })
        .then(res => {
          const { status, statusText } = res
          if (status === 200 && statusText === 'OK') {
            this.isLogin = false
            localStorage.setItem('isLogin', false)
            this.time = setInterval(() => {
              axios({
                url: `${base_url.roteador}/v1/service/dhcp/clients`,
                method: 'get'
              }).then(res => {
                const query = res.data
                this.devices = query.data
                this.ativos = query.data.filter(ativo => ativo.offline === 0)
                this.inativos = query.data.filter(ativo => ativo.offline === 1)
                axios({
                  url: `${base_url.roteador}/v1/interface/wireless/acl`,
                  method: 'get'
                }).then(res_block => {
                  const query = res_block.data
                  this.devicesBlocked = query.data.addresses
                })
              })
            }, 1000)
          } else {
            clearInterval(this.time)
            this.isLogin = true
            this.isError = false
            localStorage.setItem('isLogin', true)
            localStorage.setItem('isError', false)
            console.log(res)
          }
        })
        .catch(err => {
          if (err) {
            this.isError = true
            this.isLogin = true
            localStorage.setItem('isLogin', true)
          }
        })
        .finally(() => {})
    }
  },
  created() {
    if (
      localStorage.getItem('isLogin') === null ||
      localStorage.getItem('isLogin') == 'true'
    ) {
      clearInterval(this.time)
      this.isLogin = true
      this.isError = false
      localStorage.setItem('isLogin', true)
      localStorage.setItem('isError', false)
    } else {
      this.isLogin = false
      localStorage.setItem('isLogin', false)
      this.time = setInterval(() => {
        axios({
          url: `${base_url.roteador}/v1/service/dhcp/clients`,
          method: 'get'
        }).then(res => {
          const query = res.data
          this.devices = query.data
          this.ativos = query.data.filter(ativo => ativo.offline === 0)
          this.inativos = query.data.filter(ativo => ativo.offline === 1)
          axios({
            url: `${base_url.roteador}/v1/interface/wireless/acl`,
            method: 'get'
          }).then(res_block => {
            const query = res_block.data
            this.devicesBlocked = query.data.addresses
          })
        })
      }, 1000)
    }
  }
}
</script>

