import axios from 'axios/dist/axios'
import base_url from '../../config'

const actionsDomains = {
  actions: {
    async addItem({ commit, dispatch }, payload) {
      // desestruturação context: { commit, dispatch }
      const item = payload
      axios({
        url: base_url.api,
        method: 'post',
        data: {
          query: `
            mutation ($item: ItemInput!) {
              newItem: saveItem(item: $item) {
                id
                type
                description
              }
            }
          `,
          variables: {
            item
          }
        }
      }).then(res => {
        const query = res.data
        const newItem = query.data.newItem
        commit('addItem', { item, newItem }) // context.commit run mutation addItem
        dispatch('generateDomains')
      })
    },
    async deleteItem({ commit, dispatch }, payload) {
      // desestruturação context: { commit, dispatch }
      const item = payload
      axios({
        url: base_url.api,
        method: 'post',
        data: {
          query: `
            mutation ($id: Int!) {
              deleted: deleteItem(id: $id)
            }
          `,
          variables: {
            id: item.id
          }
        }
      }).then(() => {
        commit('deleteItem', { item }) // context.commit run mutation deleteItem
        dispatch('generateDomains')
      })
    },
    async getItems({ commit }, payload) {
      // desestruturação context: { commit, dispatch }
      const type = payload
      return axios({
        url: base_url.api,
        method: 'post',
        data: {
          query: `
            query ($type: String!) {
              items: items(type: $type) {
                id
                type
                description
              }
            }
          `,
          variables: {
            type
          }
        }
      }).then(res => {
        const query = res.data
        commit('setItems', { type, items: query.data.items }) // context.commit run mutation setItems
      })
    },
    async generateDomains({ commit }) {
      // desestruturação context: { commit }
      axios({
        url: base_url.api,
        method: 'post',
        data: {
          query: `
            mutation {
              domains: generateDomains {
                name
                checkout
                available
              }
            }
          `
        }
      }).then(res => {
        const query = res.data
        commit('setDomains', { domains: query.data.domains }) // context.commit run mutation setDomains
      })
    }
  }
}

export default actionsDomains
