<template>
  <div>
    <div class="container animated fadeIn">
      <div class="text-left">
        <router-link class="btn btn-light" to="/users">Voltar</router-link>
        <br />
        <br />
        <h2 class="text-center">{{ name.toUpperCase() }}</h2>
        <br />
        <center>
          <div class="card" style="width: 18rem;">
            <img
              class="card-img-top"
              style="height: 180px; width: 100%; display: block;"
              :src="image"
              data-holder-rendered="true"
            />
            <div class="card-body text-left">
              <h5 class="card-title">{{ name.toUpperCase() }}</h5>
              <p class="card-text">
                {{ email }}
              </p>
            </div>
          </div>
        </center>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios/dist/axios'
import base_url from '../../config'
export default {
  name: 'UserView',
  props: ['id'],
  data() {
    return {
      name: '',
      email: '',
      image: ''
    }
  },
  created() {
    axios({
      url: base_url.api,
      method: 'post',
      data: {
        query: `
          query ($id: Int!) {
            user(id: $id) {
              id
              name
              email
              image
            }
          }
        `,
        variables: {
          id: parseInt(this.id)
        }
      }
    }).then(res => {
      const query = res.data
      const user = query.data.user
      this.name = user[0].name
      this.email = user[0].email
      this.image = user[0].image
    })
  }
}
</script>
