const mutationsUsers = {
  mutations: {
    addUser(state, payload) {
      const { newUser } = payload
      state.users.push(newUser)
    },
    deleteUser(state, payload) {
      const { user } = payload
      state.users.splice(state.users.indexOf(user), 1)
    },
    setUsers(state, payload) {
      const { users } = payload
      state.users = users
    },
    MUTATION_ADD_USERSAPIJAVA(state, payload) {
      const user = payload
      state.usersapijava.push(user)
    },
    MUTATION_SET_USERSAPIJAVA(state, payload) {
      const users = payload
      state.usersapijava = users
    },
    MUTATION_REMOVE_USERAPIJAVA(state, payload) {
      const id = payload
      state.usersapijava.filter(val => {
        if (val.id === id) {
          if (state.usersapijava.indexOf(val) > -1) {
            state.usersapijava.splice(state.usersapijava.indexOf(val), 1)
          }
        }
      })
    }
  }
}

export default mutationsUsers
