<template>
  <div class="row mb-5">
    <div class="col-md-6 form-group">
      <form @submit="handleSubmit">
        <label class="mr-2">
          Nome:&nbsp;&nbsp;
          <input
            type="text"
            class="form-control"
            name="nome"
            v-model="user.nome"
          />
        </label>
        <label class="mr-2">
          E-mail:&nbsp;&nbsp;
          <input
            type="text"
            class="form-control"
            name="nome"
            v-model="user.email"
          />
        </label>
        <br />
        <button type="submit" @click="handleSubmit" class="btn btn-info">
          Enviar
        </button>
      </form>
    </div>
    <div class="col-md-6 form-group">
      <ul class="list-group">
        <li
          class="list-group-item animated bounceIn list-user-api-java"
          v-for="user in usersapijava"
          :key="user.id"
          :id="`user_${user.id}`"
        >
          <div class="row">
            <div class="col-md-5 text-left aling-center texto">
              {{ user.nome }}
            </div>
            <div class="col-md-7 text-right aling-center texto">
              {{ user.email }}
            </div>
          </div>
          <div class="row">
            <div class="col-md-12 mt-3 d-flex flex-row-reverse">
              <div class="btn-user-api-java" @click="handleDelete">
                <i class="fa fa-trash" :data-id="user.id"></i>
              </div>
              <div class="btn-user-api-java">
                <i class="fa fa-edit"></i>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'UserApiJava',
  data() {
    return {
      user: {
        nome: '',
        email: ''
      }
    }
  },
  methods: {
    handleDelete: function(e) {
      // console.log(e.target.dataset.id)
      const id = e.target.dataset.id
      this.$store.dispatch('user/ACTION_DELETE_USERAPIJAVA', id)
      document.querySelector(`#user_${id}`).classList.remove('bounceIn')
      document.querySelector(`#user_${id}`).classList.add('bounceOut')
      setTimeout(() => {
        document.querySelector(`#user_${id}`).remove()
      }, 1000)
    },
    handleSubmit: function(e) {
      e.preventDefault()
      const user = this.user
      this.$store.dispatch('user/ACTION_ADD_USERAPIJAVA', user)
    }
  },
  computed: {
    ...mapState('user', ['usersapijava'])
  },
  created() {
    this.$store.dispatch('user/ACTION_GET_USERSAPIJAVA', { root: true })
  }
}
</script>

