<template>
  <div class="form-group">
    <label>{{ label }}</label>
    <input
      class="form-control"
      :value="value"
      @input="$emit('input', $event.target.value)"
    />
  </div>
</template>

<script>
export default {
  name: 'InputField',
  props: {
    label: { type: String },
    value: { default: () => undefined }
  }
}
</script>

