import Vue from 'vue'
import Vuex from 'vuex'
import modulesVuex from './Vuex'

Vue.use(Vuex)

const moduleDomains = {
  namespaced: true,
  ...modulesVuex.statesDomains,
  ...modulesVuex.mutationsDomains,
  ...modulesVuex.actionsDomains
}

const moduleUsers = {
  namespaced: true,
  ...modulesVuex.statesUsers,
  ...modulesVuex.mutationsUsers,
  ...modulesVuex.actionsUsers
}

const store = new Vuex.Store({
  modules: {
    domain: moduleDomains,
    user: moduleUsers
  }
})

export default store
