<template>
  <div style="width: 100%;">
    <form @submit="submit" action="#" method="post">
      <template v-for="(schema, key) in schemas">
        <component
          :key="key"
          :is="schema.component"
          v-bind="schema.bind"
          v-model="record[schema.name]"
        ></component>
      </template>
      <button>Enviar</button>
    </form>
    <pre>
      <div v-for="(entry, index) in log" v-html="entry" :key="index"></div>
    </pre>
  </div>
</template>

<script>
import InputField from './InputField'

const schemas = {
  description: {
    name: 'description',
    component: 'input-field',
    bind: {
      label: 'Descrição: ',
      default: ''
    }
  },
  firstname: {
    name: 'firstName',
    component: 'input-field',
    bind: {
      label: 'Nome: ',
      default: ''
    }
  }
}

const record = Object.keys(schemas).reduce((accumulate, key) => {
  if (!schemas[key].bind) {
    return accumulate
  }
  accumulate[key] = schemas[key].bind.default
  return accumulate
}, {})

export default {
  name: 'Test',
  components: {
    InputField
  },
  data() {
    return {
      schemas,
      record,
      log: []
    }
  },
  methods: {
    submit($event) {
      this.log.push(`Save ${JSON.stringify(this.record)}`)
      $event.preventDefault()
    }
  }
}
</script>

