<template>
  <div
    class="alert alert-dismissible fade show animated"
    :class="[isShow ? 'bounceInRight' : 'bounceOutRight', type]"
    role="alert"
  >
    <strong>{{ title }}</strong
    ><br />{{ msg }}
    <div type="div" class="close">
      <div style="cursor: pointer;" aria-hidden="true" @click="close">
        &times;
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Alert',
  props: ['type', 'title', 'msg', 'isShow'],
  data() {
    return {}
  },
  methods: {
    close: function() {
      this.$props.isShow = false
    }
  }
}
</script>

<style>
div.alert {
  z-index: 1;
  position: fixed;
  top: 0;
  right: 0;
  margin-right: 8px;
  margin-top: 8px;
}
</style>
