import axios from 'axios/dist/axios'
import base_url from '../../config'

const actionsUsers = {
  actions: {
    async ACTION_DELETE_USERAPIJAVA({ commit }, payload) {
      const id = payload
      await axios({
        // url: 'http://localhost:8080/usuario',
        url: `https://api-back-jar.herokuapp.com/contatos/${id}`,
        method: 'delete'
      }).then(() => {
        commit('MUTATION_REMOVE_USERAPIJAVA', id)
      })
    },
    async ACTION_ADD_USERAPIJAVA({ commit, dispatch }, payload) {
      const { nome, email } = payload
      await axios({
        // url: 'http://localhost:8080/usuario',
        url: 'https://api-back-jar.herokuapp.com/contatos',
        method: 'post',
        data: {
          nome,
          email
        }
      }).then(res => {
        const user = res.data
        commit('MUTATION_ADD_USERSAPIJAVA', { user })
        dispatch('ACTION_GET_USERSAPIJAVA')
      })
    },
    async ACTION_GET_USERSAPIJAVA({ commit }) {
      // desestruturação context: { commit }
      axios({
        // url: 'http://localhost:8080/usuario',
        url: 'https://api-back-jar.herokuapp.com/contatos',
        method: 'get'
      }).then(res => {
        const users = res.data
        commit('MUTATION_SET_USERSAPIJAVA', users)
      })
    },
    async addUser({ commit, dispatch }, payload) {
      // desestruturação context: { commit, dispatch }
      const user = payload
      // console.log(user)
      axios({
        url: base_url.api,
        method: 'post',
        data: {
          query: `
            mutation ($user: UserInput!) {
              newUser: saveUser(user: $user) {
                id
                name
                email
                image
              }
            }
          `,
          variables: {
            user
          }
        }
      }).then(res => {
        const query = res.data
        const newUser = query.data.newUser
        commit('addUser', { newUser }) // context.commit run mutation addUser
        dispatch('generateUsers')
      })
    },
    async deleteUser({ commit, dispatch }, payload) {
      // desestruturação context: { commit, dispatch }
      const user = payload
      axios({
        url: base_url.api,
        method: 'post',
        data: {
          query: `
            mutation ($id: Int!) {
              deleted: deleteUser(id: $id)
            }
          `,
          variables: {
            id: user.id
          }
        }
      }).then(() => {
        commit('deleteUser', { user }) // context.commit run mutation deleteUser
        dispatch('generateUsers')
      })
    },
    async generateUsers({ commit }) {
      // desestruturação context: { commit }
      axios({
        url: base_url.api,
        method: 'post',
        data: {
          query: `
            query {
              users {
                id
                name
                email
              }
            }
          `
        }
      }).then(res => {
        // const query = res.data
        // this.users = query.data.users
        const query = res.data
        commit('setUsers', { users: query.data.users }) // context.commit run mutation setDomains
      })
    }
  }
}

export default actionsUsers
